@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
*{
    --mainColor: 242, 197, 124;
    --secondaryColor: 221, 174, 126;
    --thirdColor: 127, 182, 133;
    --red: 239, 111, 108;
}

body {
    padding: 0;
    margin: 0;
    height: 100vh;
}

@supports (-webkit-touch-callout: none) {
    body {
      /* The hack for Safari */
      height: -webkit-fill-available;
    }
  }
.Oeuf {
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    transition-property: top, opacity;
    transition-duration: 1s,2s;
    transform-style: ease-out;
    transition-delay: 0s,1s;
    opacity: 1;
}
.Oeufanim {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;

}

.panier {
    position: absolute;
    bottom: -5%;
    width: 200%;

}

.Oeufapp {
    width: 150px;
    aspect-ratio: 1 / 1.5;
    position: relative;
    top:60%;
    left:50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
    margin:0;
}

.up {
    top: -30%;
    opacity:0;
}

.down {
    opacity:0;
    top: 10%;
}

#tapIcon {
    position: absolute;
    top: 55%;
    left: 65%;
    transform: translate(-50%,-50%);
    color: blue;
    width: 65%;
    pointer-events: none;
}

.indicationOeuf {
    position: absolute;
    top: -40%;
    left: 10%;
    font-family: 'Indie Flower', cursive;
    font-size: 200%;
    font-weight: bold;
    white-space:nowrap;
}

#arrow {
    position: absolute;
    top: -30%;
    left: -70%;
    width: 150%;
    height: auto;
    transform: rotate(55deg);

}

@media screen and (min-width: 640px) and (min-height: 800px) {
    .Oeufapp {
        width: 300px;
    }
}
.Activity {
    width: 50%;
    top: 40%;
    aspect-ratio: 1/1.18;
    position: absolute;
    background-color: whitesmoke;
    border-radius: 15%; 
box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
transition-property: width, top, font-size;
transition-duration: 1s,1s;
transition-timing-function: ease-in-out,ease-in-out;
transition-delay: 1.2s,1.2s;
    
}


.imageActivity {
    width:100%;
    aspect-ratio: 1/1;
    border-radius: 15%;
    object-fit: cover;
}

.activityName {
    font-size: 40%;
    text-align: center;
    padding-bottom: 3px;
    font-family:'Permanent Marker', cursive;
    color:#46b3e6;
    overflow-wrap: break-word;
}



.reload {
    position: absolute;
    left: 98%;
    top: 0%;
    transform: translate(-50%,-50%);
    width: 23%;
}

.popOut {
    width: 140%;
    font-size: 240%;
    top: -30%;

}

@media screen and (min-width: 640px) and (min-height: 800px) {
    .activityName {
        font-size: 80%;
    }
}

.main {
    position:relative;
    padding:0;
    margin:0;

    top:0;
    left:0;

    width: 100%;
    height: 100%;
    position: relative;

    background-position: center;
    background-size: cover;

}
.goToAdd {
    position: absolute;
    left:5px;
    top: 5px;
    width: 50px;
}



@media (min-width: 800px)  {
    .addActivity {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}
}

.plus, .previewImg{
    border: 2px solid lightgrey;
	background-color: #fff;
	font-size: 16px;
	height: 2.5em;
	width: 2.5em;
	border-radius: 30px;
	position: relative;
    font-size: 55px;
    object-fit: cover;
}

.plus::after{
    content: "";
    display: block;
    background-color: grey;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 0.2em;
    width: 1em;
}
.plus::before {
    content: "";
    display: block;
    background-color: grey;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 1em;
    width: 0.2em;
}
.formActivity {
    position: relative;
    top: 50%;
    left: 50%;
    height: 37%;
    min-height: 250px;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.input {
    border: 2px solid rgb(180, 180, 180);
    border-radius: 8px;
    text-align:center;
    height: 28px;
    width: 100%;
    -webkit-user-select: text;
        -ms-user-select: text;
            user-select: text;
}
.inputField {
    width: 50%;
    position: relative;
}

.envoi {
    width: 25px;
    margin-top: 1px;
    position: absolute;
    right: -35px;
}

.envoi:active {
    filter: brightness(80%);
}

@media (min-width: 800px)  {
    .formActivity {
        max-width: 650px;
        width: 60%;
        height: 260px;
    position: static;
    transform: none;
    
    }
}

.ApiPopup {

    position: absolute;
    z-index:2;
    width: 60%;
    height: 70%;
    background-color:lightgray;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);

}


ul {
    list-style-type: none;
    
}



@media (min-width:320px)  { 

    .listActivity {

        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        position: absolute;
        top: 95vh;
        transition: top;
        transition-duration: 0.75s;
        transition-timing-function:ease;
        background-color: white;
    }

    .showHide{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 5vh;
        transition: transform;
        transition-duration: 0.75s;
    }

    .turn {
        transform: rotate(180deg);
    }


    .show{
        top:0;
    }

    .showHide:first-child {
        color: #0d6efd;
        
    }
    .allItems {
        padding: 0;
        width: 80%;
    }
}

@media (min-width: 800px) {
    .showHide{
        display: none;
    }

    .listActivity{
        width: 30%;
        position:relative;
        top: 0;
        height: 80%;
        border: Solid gray 1px ;
    }
    .allItems {
        padding: 0;
        width: 90%;
        margin-top: 5%;
    }
        
}
* {
    margin: 0;
    padding: 0;
    user-select:none;
    -ms-user-select:none;
    -webkit-touch-callout:none;
    -webkit-user-select:none;
    -moz-user-select:none;
}

#root {
    position:fixed;
    padding:0;
    margin:0;

    top:0;
    left:0;

    width: 100%;
    height: 100%;
}

.loadZone {
    position: absolute;
    background-color: white;
    z-index: 2;
    width: 100%;
    height: 100%;
    top:0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loadChiken {
    width: 400px;
    height: 400px;
}
